import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import { Router, NavigationEnd, NavigationStart, RoutesRecognized } from "@angular/router"
import { Broadcaster } from '../../shared/services/broadcaster.service';
import { O365SecurityService } from '../../shared/services/o365Security.service';
import * as MSGraph from "microsoft-graph"
import { InsideOffice } from "../../../../app/Modules/shared/utility/officeEnv"
import { Subscription } from 'rxjs/Subscription';
@Injectable()
export class DataRouting {
    //data
    excelUrl = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/createSession";
    persistsId: string;
    nonpersistId: string;
    navItems = []
    instructors: any[] = [];
    students: any[] = [];
    currentUser: MSGraph.User = {};
    currentStudent: any = {};
    excelData: any[] = [];
    //events
    subscription_login_success: Subscription;
    subscription_profile_photo: Subscription;

    constructor(private _router: Router, private _broadcaster: Broadcaster, private _o365SecurityService: O365SecurityService) {
        this.registerRoutingEvents();
        this.registerEvents();
    }

    registerRoutingEvents() {
        this._router.events.filter(e => e instanceof NavigationEnd).subscribe(e => {
            if (e.url.includes("home")) {
                if (Object.keys(this.currentUser).length === 0 && this.currentUser.constructor === Object) {
                    this.get("me", "/me", "", "", "", "");
                    this.post("persistsession", this.excelUrl, { "persistChanges": true }, "");
                    this.post("nonpersistsession", this.excelUrl, { "persistChanges": false }, "");
                }
                else {
                    //    
                }
            }
            else if (e.url.includes("instructors")) {
                if (this.instructors.length > 0) {
                    if (this.instructors[0].url == undefined || this.instructors[0].url == "") {
                        this.setDefaultPhoto();
                        this._o365SecurityService.getPhotos((this.instructors));
                    }
                    this._broadcaster.broadcast("instructorReady", this.instructors);
                }
                else {
                    this.get("instructors", "/users", "", "", "jobTitle eq 'instructor'", "");
                }
            }
            else if (e.url.includes("register")) {
                if (this.instructors.length > 0) {
                    this._broadcaster.broadcast("register_instructors", this.instructors);
                    this._broadcaster.broadcast("get_student_data", this.currentStudent);
                }
                else {
                    this.get("register_instructors", "/users", "", "", "jobTitle eq 'instructor'", "");
                    let excelUrl = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/columns(id='" + 1 + "')/filter/apply";
                    var data = {
                        "criteria": {
                            "filterOn": "custom",
                            "criterion1": "=" + this.currentUser.userPrincipalName + ""
                        }
                    }
                    this.post('filter', excelUrl, data, this.nonpersistId);
                    this._o365SecurityService.getProfilePhoto();
                }
            }
            else if (e.url.includes("students")) {
                if (this.students.length > 0) {
                    this._broadcaster.broadcast("get_students", this.students);
                }
                else {
                    let excelUrl = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/range/visibleView";
                    this.get("get_students", excelUrl, "", "", "", this.persistsId);
                }
            }
            else if (e.url.includes("data")) {
                let excelUrl = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/tables/2/rows";
                this.get("get_excel_data", excelUrl, "", "", "", "");
            }
            else if (e.url.includes("exceljs")) {
                let excelUrl = "drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet3/tables/2/rows";
                this.get("get_excel_data", excelUrl, "", "", "", "");
            }
        });
    }

    get(callType: string, url: string, select: string, expand: string, filter: string, sessionId) {
        if (this._o365SecurityService.authToken) {
            this._broadcaster.broadcast("spinner_on", true);
            this._o365SecurityService.get(url, select, expand, filter, sessionId).subscribe((response: any) => {
                switch (callType) {
                    case "me":
                        this.currentUser = response;
                        if (this.currentUser.userPrincipalName === "jay@officeintegrator.com") {
                            this.navItems = [
                                { name: 'Home', route: 'home' },
                                { name: 'Instructors', route: 'instructors' },
                                { name: 'Locations', route: 'locations' },
                                { name: 'Register', route: 'register' },
                                { name: 'Students', route: 'students' }
                            ]
                        }
                        else {
                            this.navItems = [
                                { name: 'Home', route: 'home' },
                                { name: 'Register', route: 'register' }
                            ]
                        }
                        this._broadcaster.broadcast("display.name", this.currentUser.displayName);
                        this._broadcaster.broadcast("navItems", this.navItems);
                        break;
                    case "instructors":
                        this.instructors = response.value;
                        this.setDefaultPhoto();
                        this._broadcaster.broadcast("instructorReady", this.instructors);
                        this._o365SecurityService.getPhotos((this.instructors));
                        break;
                    case "get_students":
                        this.students = response.text;
                        this._broadcaster.broadcast("get_students", this.students);
                        break;
                    case "register_instructors":
                        this.instructors = response.value;
                        this._broadcaster.broadcast("register_instructors", this.instructors);
                        break;
                    case "get_student_data":
                        if (response.values.length === 1) {
                            this.currentStudent = null;
                        }
                        else {
                            Object.assign(this.currentStudent, response.values[1]);
                        }
                        this._broadcaster.broadcast("get_student_data", this.currentStudent);
                        break;
                    case "get_excel_data":
                        this.excelData = response.value.map((x) => {
                            var test = {};
                            var values = x.values;
                            var arr = values[0];
                            for (let i = 0; i < arr.length; ++i) {
                                test["column" + i] = arr[i];
                            }
                            return test;
                        })
                        this._broadcaster.broadcast("get_excel_data", this.excelData);
                        break;
                }
                this._broadcaster.broadcast("spinner_on", false);
            })
        }
    }

    put(callType: string, url: string, param: any) {
        if (this._o365SecurityService.authToken) {
            this._broadcaster.broadcast("spinner_on", true);
            this._o365SecurityService.put(url, param).subscribe((response: any) => {
                switch (callType) {
                    case "photo":

                        break;
                    case "users":

                        break;
                    case "charts":
                        //url = this.worksheet + query;
                        break;
                    case "users":
                        // url = this.users;
                        break;
                    case "sendEmail":
                        // this._http.post('https://graph.microsoft.com/v1.0/me/sendMail', { 'message': "jay@officeintegrator.com", 'saveToSentItems': true }, { headers: this.headers });
                        break;
                }
                this._broadcaster.broadcast("spinner_on", false);
            })
        }
    }

    post(callType: string, url: string, param: any, sessionId: string) {
        if (this._o365SecurityService.authToken) {
            this._broadcaster.broadcast("spinner_on", true);
            this._o365SecurityService.post(url, param, sessionId).subscribe((response: any) => {
                switch (callType) {
                    case "students":
                        //this._broadcaster.broadcast("students", "Done");
                        // let url = 'users/' + this.currentUser.id;
                        // let param: any = {
                        //     "officeLocation": String(response.index)
                        // }
                        // this.patch("user_update", url, param);
                        break;
                    case "filter":
                        if (response == null) {
                            let excelUrl = 'drives/b!4T1Mn5DuG0OArpMqWqUOsNJbRaZNMyJMtN2MFg-7S22_y01huc_GR4jWibMwqcvb/items/013W6CYDMH3KYIRPKZXVE3K6OP334T7YXW/workbook/worksheets/Sheet2/tables/1/range/visibleView';
                            this.get("get_student_data", excelUrl, "", "", "", this.nonpersistId);
                        }
                        //url = this.worksheet + query;
                        break;
                    case "persistsession":
                        this.persistsId = response.id;
                        console.log(response);
                        break;
                    case "nonpersistsession":
                        this.nonpersistId = response.id;
                        console.log(response);
                        break;
                }
                this._broadcaster.broadcast("spinner_on", false);
            })

        }
    }

    patch(callType: string, url: string, param: any, sessionId: string) {
        if (this._o365SecurityService.authToken) {
            this._broadcaster.broadcast("spinner_on", true);
            this._o365SecurityService.patch(url, param, sessionId).subscribe((response: any) => {
                switch (callType) {
                    case "patch_user":
                        this._broadcaster.broadcast("student_save", "Done");
                        break;
                    case "patch_excel_data":
                        this._broadcaster.broadcast("patch_excel_data", response);
                        break;
                    case "users":
                        // url = this.users;
                        break;
                    case "students":
                        // this._http.post('https://graph.microsoft.com/v1.0/me/sendMail', { 'message': "jay@officeintegrator.com", 'saveToSentItems': true }, { headers: this.headers });
                        break;
                }
                this._broadcaster.broadcast("spinner_on", false);
            })

        }
    }
    //todo:filtet
    getUser(id: string) {
        let selectedUser: MSGraph.User
        this.instructors.filter((user: MSGraph.User) => {
            if (user.id === id) {
                selectedUser = user;
            }
        })
        return selectedUser;
    }

    registerEvents() {
        this.subscription_login_success = this._broadcaster.on<string>('login_success')
            .subscribe((result: any) => {
                if (Object.keys(this.currentUser).length === 0 && this.currentUser.constructor === Object) {
                    this.get("me", "/me", "", "", "", "");
                }
            })

        this.subscription_profile_photo = this._broadcaster.on<string>('profile_photo')
            .subscribe((result: any) => {
                this.currentStudent.url = result.url;
            })
    }

    login() {
        if (InsideOffice.isOffice) {
            this._o365SecurityService.login2();
        }
        else {
            this._o365SecurityService.login();
        }

    }

    logOut() {
        this.instructors.length = 0;
        this._o365SecurityService.logout();
    }

    //move this to utility
    setDefaultPhoto() {
        this.instructors.filter((user) => {
            user.url = 'assets/libs/download.jpeg';
        })
    }

}



